<script>
import GoogleMap from "../../components/shared/GoogleMap";
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";
import * as Chartist from 'chartist';
import * as ChartistTooltips from 'chartist-plugin-tooltips-updated';
import $ from 'jquery'
import { mapGetters } from "vuex";

export default {
  name: "Dashboard",
  components: {
    GoogleMap,
  },
  data() {
    return {
      selected: "Month",
      regions : {
        us_east_1: {
          latitude: 39.020812,
          longitude: -77.433357
        },
        us_east_2: {
          latitude: 40.4172871,
          longitude: -82.907123
        },
        us_west_1: {
          latitude: 36.778261,
          longitude: -119.4179324
        },
        us_west_2: {
          latitude: 43.8041334,
          longitude: -120.5542012
        },
        ca_central_1: {
          latitude: 51.253775,
          longitude: -85.323214
        },
        eu_central_1: {
          latitude: 50.1109221,
          longitude: 8.6821267
        },
        eu_west_1: {
          latitude: 53.4058314,
          longitude: -6.0624418
        },
        eu_west_2: {
          latitude: 51.5073509,
          longitude: -0.1277583
        },
        eu_west_3: {
          latitude: 48.856614,
          longitude: 2.3522219
        },
        eu_north_1: {
          latitude: 59.334591,
          longitude: 18.063240
        },
        ap_northeast_1: {
          latitude: 35.6894875,
          longitude: 139.6917064
        },
        ap_northeast_2: {
          latitude: 37.566535,
          longitude: 126.9779692
        },
        ap_northeast_3: {
          latitude: 34.6937378,
          longitude: 135.5021651
        },
        ap_southeast_1: {
          latitude: 1.3553794,
          longitude: 103.8677444
        },
        ap_southeast_2: {
          latitude: -33.8688197,
          longitude: 151.2092955
        },
        ap_south_1: {
          latitude: 19.0759837,
          longitude: 72.8776559
        },
        sa_east_1: {
          latitude: -23.5505199,
          longitude: -46.6333094
        }
      },
      chartOptions: {
        plugins: [
          Chartist.plugins.tooltip()
        ],
        seriesBarDistance: 10,
        axisX: {
          showGrid: false
        },
        axisY: {
          offset: 80,
          // labelInterpolationFnc: function(value) {
          //   return scope.formatNumber(value)
          // },
        },
        height: "245px",
      },
    };
  },
  computed: {
    ...mapGetters([
      // map this.count to store.state.count
      "showDashboardLoader",
      "regionData",
      "regionCount",
      "currentCost",
      "mostUsedServices",
      "barDataSet",
    ]),
    defaultDatasets () {
      return [
        {
          backgroundColor: [
            '#41B883',
            '#E46651',
            '#00D8FF',
            '#DD1B16'
          ],
          data: [40, 20, 80, 10]
        }
      ]
    },
    defaultBarDatasets () {
      return [
        {
          label: 'GEC2 - Other',
          backgroundColor: '#f87979',
          data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11]
        },
        {
          label: 'Amazon Elastic Compute Cloud - Compute',
          backgroundColor: '#087A79',
          data: [40, 20, 12, 39, 10, 40, 39, 60, 40, 20, 12, 11]
        },
      ]
    },
  },

  mounted() {
    this.$store.dispatch("callGetRegionsDetails");
    this.$store.dispatch("callGetCurrentDetail");
    this.$store.dispatch("callGetHistory");
    $(".container").mapael({
        map : {
            name : "world_countries"
        }
    });
    
    axiosInstance.get(endpoints.GET_EC2_REGIONS)
      .then((res) =>{
        let plots={}
        Object.keys(res.data.region).forEach(key => {
          let params = this.regions[key.split("-").join("_")];
          plots[key] = {
            latitude: params.latitude,
            longitude: params.longitude,
            value: [res.data.region[key], 1],
            tooltip: { content: `${key}` }
        }
        // ${key}<br />Instances: ${res.data.region[key]}
        })
        console.log("plots",plots);
        this.showEC2InstancesPerRegion(plots);
      })
      .catch((error)=> console.log(error));
  },
  methods: {
    showEC2InstancesPerRegion(sortedPlots) {
    var canvas = $(".mapregions");
    canvas.mapael({
      map: {
        name: "world_countries",
        zoom: {
          enabled: true,
          maxLevel: 10
        },
        defaultPlot: {
          attrs: {
            fill: "#004a9b"
            , opacity: 0.6
          }
        },
        defaultArea: {
          attrs: {
            fill: "#e4e4e4"
            , stroke: "#fafafa"
          }
          , attrsHover: {
            fill: "#FBAD4B"
          }
          , text: {
            attrs: {
              fill: "#505444"
            }
            , attrsHover: {
              fill: "#000"
            }
          }
        }
      },
      legend: {
        plot: [
          {
            labelAttrs: {
              fill: "#f4f4e8"
            },
            titleAttrs: {
              fill: "#f4f4e8"
            },
            cssClass: 'density',
            mode: 'horizontal',
            title: "Density",
            marginBottomTitle: 5,
            slices: [{
              label: "< 1",
              max: "0",
              attrs: {
                fill: "#36A2EB"
              },
              legendSpecificAttrs: {
                r: 25
              }
            }, {
              label: "> 1",
              min: "1",
              max: "50000",
              attrs: {
                fill: "#87CB14"
              },
              legendSpecificAttrs: {
                r: 25
              }
            }]
          }
        ]
      },
      plots: sortedPlots,
    });
  }
  },
};
</script>
